  align-items: center;
  display: none;
}

@media screen and (min-width: 768px) {
  .site-nav {
    display: flex;
  }
}

.site-nav__list {
  align-items: center;
  display: flex;
}

.site-nav__item:not(:last-child) {
  margin-right: 50px;
}

.site-nav__link {
  color: #212121;
  letter-spacing: .02em;
  cursor: pointer;
  padding-top: 32px;
  padding-bottom: 32px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.site-nav__link:hover, .site-nav__link:focus, .site-nav__link--current {
  color: #2196f3;
}

.site-nav__link--current:after {
  content: "";
  background-color: #2196f3;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

