@font-face {
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 700;
    src: url('../fonts/raleway-v28-cyrillic-700.eot'); 
    src: local(''),
         url('../fonts/raleway-v28-cyrillic-700.eot?#iefix') format('embedded-opentype'), 
         url('../fonts/raleway-v28-cyrillic-700.woff2') format('woff2'), 
         url('../fonts/raleway-v28-cyrillic-700.woff') format('woff'), 
         url('../fonts/raleway-v28-cyrillic-700.ttf') format('truetype'), 
         url('../fonts/raleway-v28-cyrillic-700.svg#Raleway') format('svg'); 
  }

  /* roboto-regular - cyrillic */
@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/roboto-v30-cyrillic-regular.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('../fonts/roboto-v30-cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../fonts/roboto-v30-cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('../fonts/roboto-v30-cyrillic-regular.woff') format('woff'), /* Modern Browsers */
         url('../fonts/roboto-v30-cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('../fonts/roboto-v30-cyrillic-regular.svg#Roboto') format('svg'); /* Legacy iOS */
  }
